html {
  overflow-x: hidden;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  font-size: 14px;
  line-height: 30px;
  font-weight: normal;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button-nav__mix {
  position: absolute;
  content: "";
  width: 45px;
  height: 45px;
  left: 85px;
  background-repeat: no-repeat;
}
.reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.page-header {
  width: 320px;
  margin: 0 auto;
  padding-top: 10px;
  padding-bottom: 10px;
}
.page-header__wrapper {
  display: flex;
}
.page-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Yanone Kaffeesatz', sans-serif;
}
.page-nav__list {
  display: none;
}
.page-nav__logo {
  margin-top: 10px;
}
.page-nav__btn {
  border: none;
  background-color: #ef7f1a;
  color: white;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  font-size: 18px;
  font-weight: 400px;
  padding: 8px;
  padding-top: 9px;
  padding-right: 25px;
  position: relative;
}
.page-nav__btn::before {
  position: absolute;
  content: "";
  background-image: url('../../img/icon/strelka.png');
  left: 58px;
  bottom: 0px;
  width: 25px;
  height: 25px;
  background-repeat: no-repeat;
  background-position: 0 0;
}
.user-block {
  display: flex;
  list-style: none;
  width: 50px;
  position: relative;
}
.user-block__link--serch {
  position: relative;
}
.user-block__link--serch::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 25px;
  left: -45px;
  bottom: -12px;
  background-image: url('../../img/icon/search.png');
  background-repeat: no-repeat;
  background-position: 0 0;
}
.user-block__link--basket {
  position: relative;
}
.user-block__link--basket::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 25px;
  left: -20px;
  bottom: -10px;
  background-image: url('../../img/icon/basket.png');
  background-repeat: no-repeat;
  background-position: 0 0;
}
.user-block__btn {
  position: absolute;
  border: none;
  left: 55px;
  top: -38px;
  cursor: pointer;
  width: 40px;
  height: 40px;
  outline: none;
}
.user-block__btn::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 3px;
  background-color: black;
  box-shadow: 0 -6px 0 rgba(0, 0, 0), 0 -12px 0 #000000;
  left: 5px;
  bottom: -4px;
  background-repeat: no-repeat;
  background-position: 0 0;
}
.page-main {
  width: 320px;
  margin: 0 auto;
}
.banner__slider {
  background-image: url('../../img/slider/img1.png');
  width: 320px;
  height: 156px;
  background-repeat: no-repeat;
  padding: 20px;
  box-sizing: border-box;
}
.banner__text {
  color: #ffffff;
  margin-top: 40px;
  width: 200px;
  line-height: 20px;
}
.banner__btn {
  border: 1px solid #ffffff;
  background-color: #ef7f1a;
  color: #ffffff;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  box-sizing: border-box;
  width: 50px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
}
.banner-box-img {
  display: flex;
  justify-content: space-between;
  z-index: 2;
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
}
.banner-box-img__one {
  background-image: url('../../img/slider/img2.jpg');
  background-repeat: no-repeat;
  width: 321px;
  height: 160px;
  border-right: 1px solid #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.banner-box-img__two {
  background-image: url('../../img/slider/img3.jpg');
  background-repeat: no-repeat;
  width: 321px;
  height: 160px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.banner-box-img__hedline {
  margin: 0;
  line-height: 10px;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  color: #ffffff;
}
.banner-box-img__link {
  position: relative;
  text-align: center;
  color: #ef7f1a;
}
.banner-box-img__link::before {
  position: absolute;
  content: '';
  width: 70px;
  height: 1px;
  background-color: #ef7f1a;
  bottom: 10px;
}
.banner-box-img__link--mod::before {
  width: 90px;
}
.button-nav {
  background-color: #000000;
  padding: 20px;
  margin-bottom: 20px;
}
.button-nav__item {
  list-style: none;
}
.button-nav__list {
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.button-nav__link {
  text-decoration: none;
}
.button-nav__link {
  display: block;
  box-sizing: border-box;
  border-bottom: 1px solid #ffffff;
  width: 200px;
  padding: 10px;
  padding-top: 60px;
  margin-bottom: 10px;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  color: #ffffff;
  font-weight: 400;
  font-size: 24px;
  text-align: center;
}
.button-nav__item {
  position: relative;
}
.button-nav__item::before {
  position: absolute;
  content: "";
  width: 45px;
  height: 45px;
  left: 85px;
  background-repeat: no-repeat;
  background-image: url('../../img/icon/icon1.png');
}
.button-nav__item:nth-child(2)::before {
  position: absolute;
  content: "";
  width: 45px;
  height: 45px;
  left: 85px;
  background-repeat: no-repeat;
  background-image: url('../../img/icon/icon2.png');
  left: 80px;
  top: 12px;
}
.button-nav__item:nth-child(3)::before {
  position: absolute;
  content: "";
  width: 45px;
  height: 45px;
  left: 85px;
  background-repeat: no-repeat;
  background-image: url('../../img/icon/icon3.png');
  left: 80px;
  top: 12px;
}
.button-nav__item:nth-child(4)::before {
  position: absolute;
  content: "";
  width: 45px;
  height: 45px;
  left: 85px;
  background-repeat: no-repeat;
  background-image: url('../../img/icon/icon4.png');
  top: 12px;
}
.button-nav__item:nth-child(5)::before {
  position: absolute;
  content: "";
  width: 45px;
  height: 45px;
  left: 85px;
  background-repeat: no-repeat;
  background-image: url('../../img/icon/icon5.png');
  top: 12px;
}
.button-nav__item:nth-child(6)::before {
  position: absolute;
  content: "";
  width: 45px;
  height: 45px;
  left: 85px;
  background-repeat: no-repeat;
  background-image: url('../../img/icon/icon6.png');
  left: 80px;
  top: 12px;
}
.categories {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
}
.categories__list {
  margin-right: 10px;
  font-size: 17px;
}
.categories__item {
  color: #000000;
}
.categories__item--active {
  color: #ef7f1a;
  border-bottom: 1px solid #ef7f1a;
}
.slider-footer__discription {
  width: 250px;
  padding-left: 18px;
  box-sizing: border-box;
  padding-right: 15px;
}
.slider-footer__content {
  position: relative;
  width: 300px;
  margin: 0;
}
.slider-footer__table {
  position: absolute;
  top: 100px;
  left: 15px;
  color: #ffffff;
  z-index: 55;
  width: 230px;
  border-collapse: collapse;
  display: none;
}
.slider-footer__th {
  border: 1px solid #ffffff;
  width: 100px;
  text-align: left;
  padding-left: 5px;
}
.slider-footer__td {
  border: 1px solid #ffffff;
  width: 100px;
  text-align: right;
  padding-right: 5px;
}
.slider-footer__box {
  position: relative;
  width: 250px;
}
.slider-footer__content::before {
  position: absolute;
  content: "";
  width: 250px;
  height: 480px;
  z-index: 25;
  background-color: rgba(0, 0, 0, 0.7);
  display: none;
}
.slider-footer__content:hover::before {
  display: block;
}
.slider-footer__content:hover + .slider-footer__discription {
  position: relative;
  z-index: 55;
  color: #ffffff;
}
.slider-footer__content:hover .slider-footer__table {
  display: block;
}
.slider-footer {
  width: 300px;
  margin: 0;
  outline: none;
}
.slider-footer__size-box {
  display: flex;
  flex-direction: column;
  outline: none;
}
.slider-footer__info {
  display: flex;
  justify-content: space-between;
  outline: none;
}
.slick-slide {
  outline: none;
}
.slider-footer__size-box {
  margin: 0;
}
.slider-footer__number-size {
  display: block;
  border: 1px solid #ef7f1a;
  line-height: 10px;
  width: 30px;
  text-align: center;
  padding-top: 2px;
  box-sizing: border-box;
  color: #ef7f1a;
  border-radius: 5px;
  font-size: 16px;
  font-weight: bold;
  margin-right: 2px;
}
.slider-footer__number-size--disable {
  border: 1px solid #bababa;
  color: #bababa;
}
.slider-footer__size {
  font-size: 18px;
  text-transform: uppercase;
}
.slider-footer__price {
  font-size: 36px;
  font-weight: bold;
  position: relative;
  right: 40px;
}
.slider-footer__price::before {
  position: absolute;
  content: "";
  background-image: url("../../img/icon/basketorange.png");
  background-repeat: no-repeat;
  width: 24px;
  height: 22px;
  right: -30px;
  top: 8px;
}
.slider-footer__hedline {
  font-size: 24px;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 10px;
}
.slider-footer__text {
  margin: 0;
  line-height: 15px;
  margin-bottom: 10px;
}
.slider-footer__number-size-box {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}
.slider-footer__link {
  position: absolute;
  top: 240px;
  left: 80px;
  color: #ef7f1a;
  text-decoration: #ef7f1a;
  z-index: 55;
  display: none;
}
.slider-footer__link::before {
  position: absolute;
  content: "";
  width: 75px;
  height: 1px;
  background-color: #ef7f1a;
  bottom: 9px;
}
.slider-footer__content:hover .slider-footer__link {
  display: block;
}
.slider-footer__content__new {
  position: relative;
}
.slider-footer__content__new::after {
  position: absolute;
  box-sizing: border-box;
  width: 30px;
  height: 15px;
  border-radius: 2px;
  content: "new";
  color: #ffffff;
  top: 50px;
  left: 10px;
  text-align: center;
  line-height: 16px;
  z-index: 70;
  background-color: #000000;
}
.slider-footer__content__sale {
  position: relative;
}
.slider-footer__content__sale::after {
  position: absolute;
  box-sizing: border-box;
  width: 30px;
  height: 15px;
  border-radius: 2px;
  content: "sale";
  color: #ffffff;
  top: 50px;
  left: 10px;
  text-align: center;
  line-height: 16px;
  z-index: 70;
  background-color: #000000;
}
.slider-footer__content__flag-franch {
  position: relative;
}
.slider-footer__content__flag-franch::before {
  position: absolute;
  content: "";
  background-image: url("../../img/icon/flamefranch.png");
  background-repeat: no-repeat;
  width: 18px;
  height: 12px;
  z-index: 99;
  top: 30px;
  left: 10px;
}
.slider-footer__content__flag-itali {
  position: relative;
}
.slider-footer__content__flag-itali::before {
  position: absolute;
  content: "";
  background-image: url("../../img/icon/flameitali.png");
  background-repeat: no-repeat;
  width: 18px;
  height: 12px;
  z-index: 99;
  top: 30px;
  left: 10px;
}
